.boss-damage-panel {
    display: flex;
}

.boss-damage-slot {
    width: 135px;
    height: 35px;
    border: 1px solid black;
    border-bottom: none;
    position: relative;
}

.boss-damage-card {
    width: 100px;
    height: 125px;
    position: absolute;
    left: 17px;
    top: 17px;
    border: 1px solid black;
}
.boss-damage-card_opened {
    background-color: #ff2fad;
}