.steps {
    width: 120px;
}
.step {
    width: 100%;
    height: 55px;
    border: 1px dashed black;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
}
.step_showed {
    border-style: solid;
}

.step__outcome {
    width: 40px;
    height: 40px;
    border: var(--border);
}