.possible-outcomes-panel {
    height: 100px;
    width: 100%;
    border: 1px solid black;
    display: flex;
    justify-content: center;
}
.possible-outcome {
    width: 40px;
    height: 40px;
    border: 1px solid black;
}
.possible-outcome_filler {
    background-color: darkblue;
}
.possible-outcome_boss-attack {
    background-color: #ff2fad;
}

.outcome-panel {
    width: 700px;
    height: 240px;
    border: 1px solid black;
    margin: 0 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.outcome-panel__container {
    display: flex;
    justify-content: center;
}
.outcome-card {
    width: 90px;
    height: 90px;
    border: 1px solid black;
    margin: 40px 10px;
    cursor: pointer;
    transition: background-color .3s;
}
.outcome-card_player-attack {
    background-color: greenyellow;
}
.outcome-card_boss-attack {
    background-color: #ff2fad;
}
.outcome-card_nothing {
    background-color: lightgrey;
}
.outcome-card_filler {
    background-color: darkblue;
}
.outcome-panel__buttons button {
    border: none;
    width: 200px;
    height: 55px;
}