header {
    display: flex;
    justify-content: center;
    font-size: 30px;
}

.main-container {
    width: 1600px;
}

.action-panel {
    display: flex;
    justify-content: center;
    margin-top: 50px;
}


.boss-trigger-panel {
    width: 200px;
    height: 40px;
    border: 1px solid black;
}

.main-panel {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    padding: 0 30px;
}

.player-panel {
    display: flex;
    flex-direction: column;
}
.player-panel__status {
    height: 50px;
    display: flex;
}
.player-panel__damage-mark {
    width: 25px;
    height: 25px;
    border: 1px solid black;
    transition: background-color .2s;
}
.player-panel__damage-mark_filled {
    background-color: black;
}
.player-aspect-panel {
    width: 200px;
}
.player-aspect {
    height: 70px;
    border: 1px solid black;
}
.player-aspect_active {
    background-color: lightgrey;
}
.player-aspect-container, .boss-aspect-container {
    display: flex;
    justify-content: flex-end;
}
.player-aspect-outcome, .boss-aspect-outcome {
    width: 30px;
    height: 40px;
    border: 1px solid black;
}
.player-aspect-outcome_success {
    background-color: greenyellow;
}
.player-aspect-outcome_failure {
    background-color: #ff2fad;
}

.center-panel {
    width: 700px;
    display: flex;
    justify-content: center;
}

.boss-panel {
    display: flex;
    flex-direction: column;
}
.boss-panel__status {
    height: 50px;
}
.boss-panel__status-line {
    display: flex;
}
.boss-aspect-panel {
    width: 200px;
}
.boss-aspect {
    height: 70px;
    border: 1px solid black;
}
.boss-aspect_active {
    background-color: lightgrey;
}

.game-result {
    font-size: 70px;
    display: flex;
    justify-content: center;
}

.phase {
    background-color: lightgrey;
    width: 180px;
    height: 40px;
    font-size: 12px;
    transition: all 1s;
    padding: 0 10px;
}

.phase_current {
    background-color: white;
    width: auto;
    height: auto;
    font-size: 30px;
}
