.player-action-panel {
    width: 200px;
    display: flex;
    flex-direction: column;
}
.player-action-button {
    width: 100px;
    height: 50px;
    cursor: pointer;
}
.player-action {
    display: flex;
    justify-content: flex-end;
}
.player-action__outcomes {
    display: flex;
}
.player-action__outcome {
    width: 42px;
    height: 50px;
    border: 1px solid black;
}